export default {
	data() {
		return {
			looplist:[],
			datalist:[],
			current:1,
			total:0
		}
	},
	//vue生命周期
	mounted() {
		//ajax
		console.log("mounted")
		//uni 在微信中 === wx  baidu ===swan  支付宝===my(蚂蚁) 头条===tt
		uni.request({
			url:"http://localhost:3000/recommends",
			success: (res) => {
				console.log(res.data)
				this.looplist =res.data
			}
		})
		
		uni.request({
			url:"http://localhost:3000/goods?_page=1&_limit=5",
			success: (res) => {
				console.log(res.header)
				this.datalist = res.data
				//如果在H5



				
				
				//如果在小程序

				this.total = res.header["X-Total-Count"]

				
			}
		})
	},
	//小程序的生命周期
	onLoad() {
		console.log("onLoad")
	},
	
	onReady() {
		console.log("onReady")
	},
	
	methods: {
		handleChangePage(id){
			console.log(id)
			uni.navigateTo({
				url:`/pages/detail/detail?id=${id}`
			})
		}
	}, 
	
	onReachBottom() {
		console.log(this.datalist.length,this.total)
		//字符串与number 转换。
		if(this.datalist.length==this.total){
			return 
		}
		console.log("到底了")
		this.current++;
		uni.request({
			url:`http://localhost:3000/goods?_page=${this.current}&_limit=5`,
			success: (res) => {
				// console.log(res.data)
				this.datalist = [...this.datalist,... res.data]
			}
		})
	},
	onPullDownRefresh() {
		console.log("下拉了")
		setTimeout(()=>{
			uni.stopPullDownRefresh()// wx.stop......
		},2000)
	}
}
